import { Express } from 'express';
import passport from 'passport';

export function RESTServer(app: Express) {
  app.get('/world', passport.authenticate('jwt', { session: true}), (_req, res) => {
    res.status(200).send({
      worlds: [{
        name: "Test World",
        online: 5,
        maxPlayers: 8,
        difficulty: "Crazy Stupid Hard"
      }]
    });
  });
}
